/* Estilos para o componente AboutUs */

#about_us {
  background-color: #313338c2; /* Altere a cor de fundo conforme necessário */
  color: #fff;
  padding: 50px 0;
}

.logo-container {
  text-align: right; /* Alinhe o logo à direita */
}

.about-container {
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column-reverse; /* Altere a direção da coluna conforme necessário */
}

.about-title {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 20px;
  line-height: 1.2; /* Aumente o espaçamento entre linhas */
}

.leading-relaxed {
  font-size: 20px;
  line-height: 1.3;
  margin-bottom: 5px; /* Aumente o espaçamento entre parágrafos */
  text-align: justify;
}

.about-image {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}

/* Estilização do ícone */

.book-icon {
  display: inline-block;
  width: 40px;
  margin-bottom: 10px;
  fill: #201a0ea0; /* Altere a cor do ícone conforme necessário */
}

/* Responsividade */

@media (min-width: 768px) {
  .about-container {
    flex-direction: row;
  }

  .about-content {
    text-align: justify;
  }
}

@media (max-width: 768px) {
  .logo-container {
    text-align: center; /* Alinhe o logo ao centro em telas menores */
  }

  .logo-image {
    display: block;
    margin: 0 auto; /* Centralize o logo em telas menores */
    max-width: 250px; /* Ajuste o tamanho máximo do logo */
  }

  /* Corrija o seletor .md:w-1/2 para .flex-wrap > div */

  .flex-wrap > div {
    width: 100%; /* Ocupar toda a largura em telas menores */
    padding: 0 20px; /* Adicione um espaçamento horizontal */
  }
}

/* purgecss end ignore */


