/* Projects.css */

.modal {
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  position: center;
}

.modal-content {
  max-width: 100%;
  max-height: 100%;
  overflow: auto;
  background-color: transparent;
  border-radius: 10px;
  position: center;
}

.close-button {
  width: 100%;
  position: top;
  color: white;
  background-color: rgba(86, 71, 178, 0.278);
  padding: 8px 2px;
  border-radius: 5px;
  cursor: pointer;
}

.title-image {
  width: 100%;
  position: absolute;
  top: -48px; /* Ajuste a altura conforme necessário */
  color: white;
  background-color: rgba(86, 71, 178, 0.278);
  cursor: pointer;
  font-size: 24px; /* Defina o tamanho da fonte desejado */
}

.image-capa {
  width: 100%;
  height: auto;
  max-height: 400px;
}

.carousel-container {
  position: relative;
}

.carousel-card {
  position: relative;
  border-radius: 0; /* Remova o border-radius */
  overflow: visible; /* Altere o overflow para visible */
}

.carousel-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.carousel-card:hover .carousel-overlay {
  opacity: 1;
}

.carousel-container {
  position: relative;
}

.carousel-image {
  -o-object-fit: contain;
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
}

.carousel-control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.carousel-control:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.project-title {
  font-size: 2.5rem; /* Tamanho da fonte */
  font-weight: bold; /* Peso da fonte */
  color: #ff9900; /* Cor do texto */
  text-transform: uppercase; /* Transforma o texto em maiúsculas */
  letter-spacing: 2px; /* Espaçamento entre letras */
  margin-bottom: 1rem; /* Margem inferior para espaçamento */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* Sombra do texto */
}

/* purgecss end ignore */


