/* BackgroundCarousel.css */

.carousel-background {
  position: fixed;
  top: 1;
  left: 1;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
}

.carousel-background .carousel .slider-wrapper {
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.carousel-background .carousel .slider-wrapper img {
  max-width: 100%;
  max-height: 100%;
  -o-object-fit: contain;
  object-fit: contain;
}

